import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Http, Headers } from '@angular/http';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';

import { IngredientComponent } from './../ingredient/ingredient.component';
import { OptionalsComponent } from './../ingredient/optionals/optionals.component';

import { Translation, Product, Measure, Alert } from './../shared/model';
import { ApiService, ConfigService, SharedService, DataService } from './../shared/service';
import { DropdownOption } from './../shared/enum';

@Component({
  selector: 'my-product',
  host: { '( window:keydown )': 'keydown($event)' },
  template: require('./product.component.html'),
  styles: [require('./product.component.scss')]
})
export class ProductComponent implements OnInit {
  id: number;
  action: string;
  title: string;
  text: string;
  posttext: string;
  showMeasures: boolean = false;

  searching = false;
  searchFailed = false;
  searchTerm: any;


  product: Product = new Product(0, '', 0, 0, 0, 0, 0, 0, 0, 0, [], [], [], [], [], []);

  ingredients: any[] = [];
  nutrients: any[] = [];
  tags: any[] = [];
  measures: Measure[] = [];

  lastContainer: any[] = null;
  addToLastContainer: boolean = true;

  nOptions: any[] = [];
  tOptions: any[] = [];
  mOptions: any[] = [];
  cOptions: any[] = [];
  iOptions: any[] = [];

  constructor(private route: ActivatedRoute,
    private api: ApiService,
    private config: ConfigService,
    private shared: SharedService,
    private data: DataService,
    private modal: NgbModal,
    private http: Http) {
    // Do stuff
  }

  ngOnInit(): void {
    // initiate params
    this.route.params.forEach((params: Params) => {
      this.action = params['action'];
      this.id = params['id'];
    });

    this.lastContainer = this.ingredients;

    // fill dropdown values
    this.cOptions = this.data.countries.map(f => {
      return {
        id: f.id,
        name: f.names.filter(tr => tr.language.id === this.config.language.id)[0].value
      };
    });
    this.cOptions = [{ id: 0, name: 'Choose one' }].concat(this.cOptions);

    this.tOptions = this.data.tags.map(f => {
      return {
        id: f.id,
        name: f.names.filter(tr => tr.language.id === this.config.language.id)[0].value
      };
    });

    this.nOptions = this.data.nutrients.map(f => {
      return {
        id: f.id,
        name: f.names.filter(tr => tr.language.id === this.config.language.id)[0].value
      };
    });

    this.mOptions = this.data.measures.map(f => {
      return {
        id: f.id,
        name: f.short
      };
    });
    this.mOptions = [{ id: 0, name: 'Choose one' }].concat(this.mOptions);

    this.api.getProduct(this.id).subscribe(product => {
      this.product = new Product(product.id, '', product.weight, product.content, product.measure_id, product.country_id,
        product.content_netto, product.measure_netto_id, product.nutrient_netto, product.nutrient_netto_id, [], [], [], [], [], []);

      if (product.manufacturer) {
        this.product.manufacturer = product.manufacturer.name;
      }

      for (let i = 0; i < product.info.length; i++) {
        let language = this.data.languages.filter(lang => lang.active && lang.id === product.info[i].language_id)[0];

        if (language) {
          let nameTranslation = new Translation(language, product.info[i].name);
          let textTranslation = new Translation(language, product.info[i].text);
          let postTextTranslation = new Translation(language, product.info[i].posttext);

          this.product.names.push(nameTranslation);
          this.product.texts.push(textTranslation);
          this.product.posttexts.push(postTextTranslation);
        }
      }

      this.title = this.product.names.filter(p => p.language.id === this.config.language.id)[0].value;

      for (let i = 0; i < product.nutrients.length; i++) {
        let nutrient = {
          id: product.nutrients[i].id,
          value: product.nutrients[i].pivot.amount,
          name: product.nutrients[i].info.filter(l => l.pivot.language_id === this.config.language.id)[0].pivot.name
        };

        this.nutrients.push(nutrient);
        this.updateNutrientOptions(nutrient.id, 0);
      }

      for (let i = 0; i < product.tags.length; i++) {
        let tag = {
          id: product.tags[i].id,
          name: product.tags[i].info.filter(l => l.pivot.language_id === this.config.language.id)[0].pivot.name
        };

        this.tags.push(tag);
        this.updateTagOptions(tag.id, 0);
      }

      for (let i = 0; i < product.ingredients.length; i++) {
        let ingredient = {
          id: product.ingredients[i].id,
          name: product.ingredients[i].info.filter(l => l.language_id === this.config.language.id)[0].name,
          country_id: product.ingredients[i].pivot.country_id,
          amount: product.ingredients[i].pivot.amount,
          position: product.ingredients[i].pivot.position,
          isContainer: <Boolean>product.ingredients[i].parent,
          parent_id: 0,
          level: 0,
          children: []
        };

        let children = product.ingredients[i].children;

        if (children) {
          for (let j = 0; j < children.length; j++) {
            let subingredient = {
              id: children[j].id,
              name: children[j].info.filter(l => l.language_id === this.config.language.id)[0].name,
              country_id: children[j].pivot.country_id,
              amount: children[j].pivot.amount,
              position: children[j].pivot.position,
              isContainer: <Boolean>children[j].parent,
              parent_id: ingredient.id,
              level: 1,
              children: []
            }

            let children2 = product.ingredients[i].children[j].children;

            if (children2) {
              for (let k = 0; k < children2.length; k++) {
                let sub2ingredient = {
                  id: children2[k].id,
                  name: children2[k].info.filter(l => l.language_id === this.config.language.id)[0].name,
                  country_id: children2[k].pivot.country_id,
                  amount: children2[k].pivot.amount,
                  position: children2[k].pivot.position,
                  isContainer: <Boolean>children2[k].parent,
                  parent_id: subingredient.id,
                  level: 2,
                  children: []
                }

                let children3 = product.ingredients[i].children[j].children[k].children;

                if (children3) {
                  for (let m = 0; m < children3.length; m++) {
                    let sub3ingredient = {
                      id: children3[m].id,
                      name: children3[m].info.filter(l => l.language_id === this.config.language.id)[0].name,
                      country_id: children3[m].pivot.country_id,
                      amount: children3[m].pivot.amount,
                      position: children3[m].pivot.position,
                      isContainer: <Boolean>children3[m].parent,
                      parent_id: sub2ingredient.id,
                      level: 3,
                      children: []
                    }

                    let children4 = product.ingredients[i].children[j].children[k].children[m].children;

                    if (children4) {
                      for (let n = 0; n < children4.length; n++) {
                        let sub4ingredient = {
                          id: children4[n].id,
                          name: children4[n].info.filter(l => l.language_id === this.config.language.id)[0].name,
                          country_id: children4[n].pivot.country_id,
                          amount: children4[n].pivot.amount,
                          position: children4[n].pivot.position,
                          isContainer: <Boolean>children4[n].parent,
                          parent_id: sub3ingredient.id,
                          level: 4,
                          children: []
                        }

                        sub3ingredient.children.push(sub4ingredient);
                      }

                    }
                    this.iOptions.push(sub3ingredient);
                    sub2ingredient.children.push(sub3ingredient);
                  }
                }

                this.iOptions.push(sub2ingredient);
                subingredient.children.push(sub2ingredient);
              }
            }
            ingredient.children.push(subingredient);
            this.iOptions.push(subingredient);
          }
        }

        this.ingredients.push(ingredient);
        this.iOptions.push(ingredient);
        this.sortIngredients(this.ingredients);
      }
    }, () => {
      this.shared.addAlert(new Alert(0, 'danger', 'Can\'t load product!'));
    });
  }

  keydown($event) {
    if ($event.keyCode == 83 && $event.ctrlKey) {
      $event.preventDefault();
      this.save();
    }
  }

  save() {
    if (this.ingredients.length > 0 && new Set(this.ingredients.map(i => i.id)).size !== this.ingredients.length) {
      this.shared.addAlert(new Alert(0, 'danger', 'Product has duplicate non-container ingredients'));
      return;
    }

    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    this.http.put(this.config.baseUrl + '/product/' + this.product.id, {
      data: {
        language_id: this.config.language.id,
        texts: this.product.texts,
        posttexts: this.product.posttexts,
        tags: this.tags,
        nutrients: this.nutrients,
        ingredients: this.ingredients,
        country_id: this.product.country_id,
        content: this.product.content,
        measure_id: this.product.measure_id,
        content_netto: this.product.content_netto,
        measure_netto_id: this.product.measure_netto_id,
        nutrient_netto: this.product.nutrient_netto,
        nutrient_netto_id: this.product.nutrient_netto_id,
        weight: this.product.weight,
        manu_name: this.product.manufacturer,
      }
    }, { headers }).subscribe(() => {
      this.shared.addAlert(new Alert(0, 'success', 'Product saved successfully!'));
    }, (error) => {
      this.shared.addAlert(new Alert(0, 'danger', 'Product could not be saved. ERROR -> ' + error._body));
    });
  }

  toggleOptions(options: any[], currentId: number, type: number, parentId: number, $event): void {
    if (options.length === 0) {
      return;
    }

    if ($event.key !== 'ArrowDown' && $event.key !== 'ArrowUp') {
      return;
    }

    $event.preventDefault();
    let index = options.map(i => i.id).indexOf(currentId);
    index = index + ($event.key === 'ArrowDown' ? 1 : -1);
    if (index > options.length - 1) {
      index = 0;
    }
    if (index < 0) {
      index = options.length - 1;
    }

    // choose action
    if (type === DropdownOption.ProductCountry) {
      this.changeCountryValue(options[index].id);
    } else if (type === DropdownOption.Measure) {
      this.changeMeasureValue(options[index].id);
    } else if (type === DropdownOption.MeasureNetto) {
      this.changeMeasureNettoValue(options[index].id);
    } else if (type === DropdownOption.NutrientNetto) {
      this.changeNutrientNettoValue(options[index].id);
    } else if (type === DropdownOption.Nutrient) {
      let nutrient = this.nutrients.find(n => n.id === parentId);
      this.changeNutrientValue(nutrient, options[index]);
    } else if (type === DropdownOption.Tag) {
      let tag = this.tags.find(t => t.id === parentId);
      this.changeTagValue(tag, options[index]);
    }
  }

  getCountryName(id: number) {
    if (id === null || id === undefined || id === 0) {
      return 'Choose one';
    }
    return this.data.countries.filter(c => c.id === id)[0].names.filter(l => l.language.id === this.config.language.id)[0].value;
  }

  toggleMeasure() {
    this.showMeasures = true;
  }

  changeCountryValue(id: number) {
    this.product.country_id = id;
  }

  changeIngredientCountryValue(ingredient: any, cid: number) {
    ingredient.country_id = cid;
  }

  updateNutrientOptions(id: number, oldId: number) {
    let index: number = this.nOptions.map(n => n.id).indexOf(id);
    if (index > -1) {
      this.nOptions.splice(index, 1);
      if (oldId > 0) {
        let nutrient = this.data.nutrients.filter(n => n.id === oldId)[0];
        if (nutrient) {
          this.nOptions.push({
            id: nutrient.id,
            name: nutrient.names.filter(tr => tr.language.id === this.config.language.id)[0].value
          });
        }
      }
    } else {
      let nutrient = this.data.nutrients.filter(n => n.id === id)[0];
      if (nutrient) {
        this.nOptions.push({
          id: nutrient.id,
          name: nutrient.names.filter(tr => tr.language.id === this.config.language.id)[0].value
        });
      }
    }
  }

  updateTagOptions(id: number, oldId: number) {
    let index: number = this.tOptions.map(n => n.id).indexOf(id);
    if (index > -1) {
      this.tOptions.splice(index, 1);
      if (oldId > 0) {
        let tag = this.data.tags.filter(n => n.id === oldId)[0];
        if (tag) {
          this.tOptions.push({
            id: tag.id,
            name: tag.names.filter(tr => tr.language.id === this.config.language.id)[0].value
          });
        }
      }
    } else {
      let tag = this.data.tags.filter(n => n.id === id)[0];
      if (tag) {
        this.tOptions.push({
          id: tag.id,
          name: tag.names.filter(tr => tr.language.id === this.config.language.id)[0].value
        });
      }
    }
  }

  addIngredient(id: number, name: string, isContainer = false): void {

    let container = this.addToLastContainer ? this.lastContainer : this.ingredients;

    let position = container.length > 0 ? container[container.length - 1].position + 1 : 0;

    let ingredient = {
      id: id,
      name: name,
      country_id: 0,
      amount: "",
      position: position,
      isContainer: isContainer,
      parent_id: 0,
      level: 0,
      children: []
    }

    container.push(ingredient);
    this.iOptions.push(ingredient);
  }

  deleteIngredient(container: any[], id: number): void {
    let index: number = container.map(n => n.id).indexOf(id);
    if (index > -1) {
      container.splice(index, 1);
    }
    index = this.iOptions.map(n => n.id).indexOf(id);
    if (index > -1) {
      this.iOptions.splice(index, 1);
    }
  }

  addToContainer(ingredient, container) {
    let index: number = this.ingredients.map(n => n.id).indexOf(ingredient.id);
    if (index > -1) {
      ingredient.parent_id = container.id;
      ingredient.level = container.level + 1;
      this.ingredients.splice(index, 1);
      ingredient.position = container.children.length > 0 ? container.children[container.children.length - 1].position + 1 : 0;
      container.children.push(ingredient);
      this.lastContainer = container.children;
    }
  }

  removeFromContainer(ingredient, container) {
    let index: number = container.children.map(n => n.id).indexOf(ingredient.id);
    if (index > -1) {
      ingredient.parent_id = 0;
      ingredient.level = 0;
      container.children.splice(index, 1);
      ingredient.position = this.ingredients.length > 0 ? this.ingredients[this.ingredients.length - 1].position + 1 : 0;
      this.ingredients.push(ingredient);
    }
  }

  moveToContainer(ingredient, from, to) {
    let index: number = from.children.map(n => n.id).indexOf(ingredient.id);
    if (index > -1) {
      ingredient.parent_id = to.id;
      ingredient.level = to.level + 1;
      from.children.splice(index, 1);
      to.children.push(ingredient);
      ingredient.position = to.children.length > 0 ? to.children[to.children.length - 1].position + 1 : 0;
      this.lastContainer = to.children;
    }
  }

  openIngredientOptionalsModal(ingredient): void {
    const modalRef = this.modal.open(OptionalsComponent, { size: 'lg' });
    modalRef.result.then((result) => {
      ingredient.country_id = result.cid;
      ingredient.amount = result.amount;
    });

    modalRef.componentInstance.cid = ingredient.country_id;
    modalRef.componentInstance.amount = ingredient.amount;
    modalRef.componentInstance.cOptions = this.cOptions;
  }

  sortIngredients(container_list) {
    container_list.sort((a, b) => {
      if (a.position > b.position) {
        return 1;
      } else if (a.position < b.position) {
        return -1;
      } else {
        if (a.name.toUpperCase() > b.name.toUpperCase()) {
          return 1;
        } else {
          return -1;
        }
      }
    });
  };

  moveUp(ingredient, container_list) {
    if (container_list.length > 1) {
      let index = container_list.map(i => i.id).indexOf(ingredient.id);
      if (index < 1) {
        return;
      }
      let swap = container_list[index - 1].position;
      container_list[index - 1].position = container_list[index].position;
      container_list[index].position = swap;
      this.sortIngredients(container_list);
    }
  };

  moveDown(ingredient, container_list) {
    if (container_list.length > 1) {
      let index = container_list.map(i => i.id).indexOf(ingredient.id);
      if (index === container_list.length - 1) {
        return;
      }
      let swap = container_list[index + 1].position;
      container_list[index + 1].position = container_list[index].position;
      container_list[index].position = swap;
      this.sortIngredients(container_list);
    }
  };

  openIngredientModal(name: string, id: number, action: string): void {
    const modalRef = this.modal.open(IngredientComponent, { size: 'lg' });
    modalRef.result.then((result) => {
      if (action === 'new') {
        let newName = result.info.filter(l => l.language_id === this.config.language.id)[0].name;
        this.addIngredient(result.id, newName, result.parent);
      } else {
        let newName = result.info.filter(l => l.language_id === this.config.language.id)[0].name;
        this.ingredients.filter(i => i.id === result.id)[0].name = newName;
      }
    });

    modalRef.componentInstance.mdl = true;
    modalRef.componentInstance.name = name;
    modalRef.componentInstance.action = action;
    modalRef.componentInstance.id = id;
  }

  addNutrient(): void {
    this.nutrients.push({ id: 0, name: 'Choose one', value: 0 });
  }

  changeNutrientValue(nutrient, nOption): void {
    let oldId = nutrient.id;
    nutrient.id = nOption.id;
    nutrient.name = nOption.name;
    this.updateNutrientOptions(nutrient.id, oldId);

  }

  deleteNutrient(id: number): void {
    let index: number = this.nutrients.map(n => n.id).indexOf(id);
    if (index > -1) {
      this.nutrients.splice(index, 1);
      let nutrient = this.data.nutrients.filter(n => n.id === id)[0];
      if (nutrient !== null && nutrient || undefined) {
        this.nOptions.push({
          id: nutrient.id,
          name: nutrient.names.filter(tr => tr.language.id === this.config.language.id)[0].value
        });
      }
    }
  }

  addTag(): void {
    this.tags.push({ id: 0, name: 'Choose one...' });
  }

  changeTagValue(tag, tOption) {
    let oldId = tag.id;
    tag.id = tOption.id;
    tag.name = tOption.name;
    this.updateTagOptions(tag.id, oldId);
  }

  deleteTag(id: number): void {
    let index: number = this.tags.map(t => t.id).indexOf(id);
    if (index > -1) {
      this.tags.splice(index, 1);
      let tag = this.data.tags.filter(n => n.id === id)[0];
      if (tag !== null && tag || undefined) {
        this.tOptions.push({
          id: tag.id,
          name: tag.names.filter(tr => tr.language.id === this.config.language.id)[0].value
        });
      }
    }
  }

  changeMeasureValue(id: number): void {
    this.product.measure_id = id;
  }

  changeMeasureNettoValue(id: number): void {
    this.product.measure_netto_id = id;
  }

  changeNutrientNettoValue(id: number): void {
    this.product.nutrient_netto_id = id;
  }

  getMeasureName(mid: number) {
    if (!mid) {
      return 'Choose one';
    }
    return this.data.measures.filter(m => m.id === mid)[0].short;
  }

  onSearchEnter(ingredient: any): void {
    this.searchTerm = '';
    if (ingredient && ingredient.id) {
      this.addIngredient(ingredient.id, ingredient.name);
    } else {
      this.openIngredientModal(ingredient, 0, 'new');
    }
  }

  search = (text$: Observable<string>) => {
    return text$
      .debounceTime(300)
      .distinctUntilChanged()
      .do(() => this.searching = true)
      .switchMap(term => {
        return this.api.search(term, this.config.language.id, 'ingredient')
          .do(() => {
            this.searchFailed = false;
          })
          .catch(() => {
            this.searchFailed = true;
            return Observable.of([]);
          });
      })
      .do(() => this.searching = false);
  }

  searchMan = (text$: Observable<string>) => {
    return text$
      .debounceTime(300)
      .distinctUntilChanged()
      .do(() => this.searching = true)
      .switchMap(term => {
        return this.api.search(term, this.config.language.id, 'manufacturer')
          .do(() => {
            this.searchFailed = false;
          })
          .catch(() => {
            this.searchFailed = true;
            return Observable.of([]);
          });
      })
      .do(() => this.searching = false);
  }

  onSearchManEnter(result: any) {
    this.product.manufacturer = result.name;
  }

  formatter = (result: any) => result.name;

}



