import { Injectable } from '@angular/core';
import { Http, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/of';

import { ConfigService } from './config.service';

@Injectable()
export class ApiService {
  title = 'Angular 2';
  language = null;
  shared: any = {
    tags: []
  };

  // will use this for TypeAhead and autocomplete feature
  constructor(private http: Http, private config: ConfigService) {

  }

  /**
   * Searches for a model object.
   * @param term { string } - Search term
   * @param language { number } - Id of the language of the search term
   * @param type { string }(Optional) - Type of model object it should search
   * @return Observable<any[]> - List of observable model objects. Default (if term is empty) is empty observable list.
   */
  search(term: string, language: number, type: string): Observable<any[]> {
    if (term === '') {
      return Observable.of([]);
    }

    let params = new URLSearchParams();
    params.set('q', term);
    params.set('lang', language.toString());
    params.set('type', type);

    return this.http
      .get(this.config.baseUrl + '/search', { search: params })
      .map(response => response.json());
  }

  /**
   * Retrieves all model objects.
   * @param language { number } - Id of the language - returned objects will be returned in this language.
   * @return Promise<any[]> - Promise of model objects
   */
  retrieveAll(language: number): Observable<any[]> {
    return this.http.get(this.config.baseUrl + '/search?lang=' + language)
      .map(response => response.json())
      .catch(this.handleError);
  }

  getLanguages(): Observable<any[]> {
    return this.http.get(this.config.baseUrl + '/language')
      .map(response => response.json())
      .catch(this.handleError);
  }

  getProduct(id: Number): Observable<any> {
    return this.http.get(this.config.baseUrl + '/product/' + id)
      .map(response => response.json())
      .catch(this.handleError);
  }

  getIngredients(): Observable<any[]> {
    return this.http.get(this.config.baseUrl + '/ingredient')
      .map(response => response.json())
      .catch(this.handleError);
  }

  getIngredient(id: Number): Observable<any> {
    return this.http.get(this.config.baseUrl + '/ingredient/' + id)
      .map(response => response.json())
      .catch(this.handleError);
  }

  getMeasures(): Observable<any[]> {
    return this.http.get(this.config.baseUrl + '/measure')
      .map(response => response.json())
      .catch(this.handleError);
  }

  getMeasure(id: Number): Observable<any> {
    return this.http.get(this.config.baseUrl + '/measure/' + id)
      .map(response => response.json())
      .catch(this.handleError);
  }

  getTags(): Observable<any[]> {
    return this.http.get(this.config.baseUrl + '/tag')
      .map(response => response.json())
      .catch(this.handleError);
  }

  getTag(id: Number): Observable<any> {
    return this.http.get(this.config.baseUrl + '/tag/' + id)
      .map(response => response.json())
      .catch(this.handleError);
  }

  getNutrients(): Observable<any[]> {
    return this.http.get(this.config.baseUrl + '/nutrient')
      .map(response => response.json())
      .catch(this.handleError);
  }

  getNutrient(id: Number): Observable<any> {
    return this.http.get(this.config.baseUrl + '/nutrient/' + id)
      .map(response => response.json())
      .catch(this.handleError);
  }

  getFeatures(): Observable<any[]> {
    return this.http.get(this.config.baseUrl + '/feature')
      .map(response => response.json())
      .catch(this.handleError);
  }

  getFeature(id: Number): Observable<any> {
    return this.http.get(this.config.baseUrl + '/feature/' + id)
      .map(response => response.json())
      .catch(this.handleError);
  }

  getCountries(): Observable<any[]> {
    return this.http.get(this.config.baseUrl + '/country')
      .map(response => response.json())
      .catch(this.handleError);
  }

  getCountry(id: Number): Observable<any> {
    return this.http.get(this.config.baseUrl + '/country/' + id)
      .map(response => response.json())
      .catch(this.handleError);
  }

  private handleError(error: any): Observable<any> {
    console.error('An error occurred', error);
    return error.next(error.message || error);
  }
}



