import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Http, Headers } from '@angular/http';

import { Translation, Measure, Alert } from './../shared/model';
import { ApiService, ConfigService, SharedService, DataService } from './../shared/service';


@Component({
  selector: 'my-measure',
  template: require('./measure.component.html'),
  styles: [require('./measure.component.scss')]
})
export class MeasureComponent implements OnInit {

  /**
   * router params
   * id - model id
   * action - 'create' or 'update'
   */
  id: number;
  action: string;

  measure: Measure = new Measure(0, '', [], '');

  constructor(private route: ActivatedRoute,
    private http: Http,
    private api: ApiService,
    private data: DataService,
    private shared: SharedService,
    private config: ConfigService) {
  }

  ngOnInit(): void {
    console.log('Initiated measure component');

    this.route.params.forEach((params: Params) => {
      this.action = params['action'];
      this.id = params['id'];
    });

    if (this.action === 'edit') {
      this.api.getMeasure(this.id).subscribe(measure => {
        this.measure = new Measure(measure.id, measure.code, [], measure.short);
        for (let i = 0; i < measure.info.length; i++) {
          let language = this.data.languages.filter(lang => lang.active && lang.id === measure.info[i].language_id)[0];
          if (language) {
            let translation = new Translation(language, measure.info[i].name);
            this.measure.names.push(translation);
          }
        }
      });
    } else {
      for (let i = 0; i < this.data.languages.filter(l => l.active).length; i++) {
        this.measure.names.push(new Translation(this.data.languages[i], ''));
      }
    }
  }

  save() {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    if (this.action === 'edit') {
      this.http.put(this.config.baseUrl + '/measure/' + this.measure.id, {
        data: this.measure
      }, { headers }).subscribe(() => {
        this.shared.addAlert(new Alert(0, 'success', 'Measure saved successfully!'));
        this.data.updateMeasures();
      }, (error) => {
        this.shared.addAlert(new Alert(0, 'danger', 'Measure could not be saved. ERROR -> ' + error._body));
      });
    } else {
      this.http.post(this.config.baseUrl + '/measure', {
        data: this.measure
      }, { headers }).subscribe(() => {
        this.shared.addAlert(new Alert(0, 'success', 'Measure saved successfully!'));
        this.data.updateMeasures();
      }, (error) => {
        this.shared.addAlert(new Alert(0, 'danger', 'Measure could not be saved. ERROR -> ' + error._body));
      });
    }
  }

  delete() {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    this.http.delete(this.config.baseUrl + '/measure/' + this.measure.id, { headers })
      .subscribe(() => {
        this.data.updateMeasures();
      }, () => {
        this.shared.addAlert(new Alert(0, 'danger', 'Measure could not be deleted due to foreign key constraints'));
      });
  }

}



