import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Http, Headers } from '@angular/http';

import { Translation, Country, Alert } from './../shared/model';
import { ApiService, ConfigService, SharedService, DataService } from './../shared/service';



@Component({
  selector: 'my-country',
  template: require('./country.component.html'),
  styles: [require('./country.component.scss')]
})
export class CountryComponent implements OnInit {

  /**
   * router params
   * id - model id
   * action - 'create' or 'update'
   */
  id: number;
  action: string;

  // country model
  country: Country = new Country(0, '', []);

  // constructor
  constructor(private route: ActivatedRoute,
    private http: Http,
    private api: ApiService,
    private shared: SharedService,
    private data: DataService,
    private config: ConfigService) {
  }

  // ngOnInit
  ngOnInit(): void {

    this.route.params.forEach((params: Params) => {
      this.action = params['action'];
      this.id = params['id'];
    });

    if (this.action === 'edit') {
      this.api.getCountry(this.id).subscribe(country => {
        this.country = new Country(country.id, country.code, []);
        for (let i = 0; i < country.info.length; i++) {
          let language = this.data.languages.filter(lang => lang.active && lang.id === country.info[i].language_id)[0];
          if (language) {
            let translation = new Translation(language, country.info[i].name);
            this.country.names.push(translation);
          }
        }
      });
    } else {
      for (let i = 0; i < this.data.languages.filter(l => l.active).length; i++) {
        this.country.names.push(new Translation(this.data.languages[i], ''));
      }
    }
  }

  // save
  save() {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    if (this.action === 'edit') {
      this.http.put(this.config.baseUrl + '/country/' + this.country.id, {
        data: this.country
      }, { headers }).subscribe(() => {
        this.shared.addAlert(new Alert(0, 'success', 'Country saved successfully!'));
        this.data.updateCountries();
      }, (error) => {
        this.shared.addAlert(new Alert(0, 'danger', 'Country could not be saved. ERROR -> ' + error._body));
      });
    } else {
      this.http.post(this.config.baseUrl + '/country', {
        data: this.country
      }, { headers }).subscribe(() => {
        this.shared.addAlert(new Alert(0, 'success', 'Country saved successfully!'));
        this.data.updateCountries();
      }, (error) => {
        this.shared.addAlert(new Alert(0, 'danger', 'Country could not be saved. ERROR -> ' + error._body));
      });
    }
  }

  // delete
  delete() {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    this.http.delete(this.config.baseUrl + '/country/' + this.country.id, { headers })
      .subscribe(() => { }, () => {
        this.data.updateCountries();
        this.shared.addAlert(new Alert(0, 'danger', 'Country could not be deleted due to foreign key constraints'));
      });
  }



}



