import { Component, OnInit, Input } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ConfigService, DataService } from './../../shared/service';

@Component({
  selector: 'my-ingredient-optionals',
  template: require('./optionals.component.html'),
  styles: [require('./optionals.component.scss')],
})
export class OptionalsComponent implements OnInit {

  @Input()
  cid: Number;

  @Input()
  amount: Number;

  @Input()
  cOptions: any;

  constructor(private data: DataService, private config: ConfigService, public activeModal: NgbActiveModal) {

  }

  ngOnInit() {

  }

  save() {
    this.activeModal.close({
      cid: this.cid,
      amount: this.amount
    });
  }

  getCountryName(id: number) {
    if (id === null || id === undefined || id === 0) {
      return 'Choose one';
    }
    return this.data.countries.filter(c => c.id === id)[0].names.filter(l => l.language.id === this.config.language.id)[0].value;
  }

  updateCountry(id: number) {
    this.cid = id;
  }

}



