import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/Rx';

import { Alert } from './../model';

@Injectable()
export class SharedService {

    private dataStore: {
        alerts: Array<Alert>;
    };

    private _alerts: BehaviorSubject<Alert[]>;

    constructor() {
        this.dataStore = { alerts: [] };
        this._alerts = <BehaviorSubject<Alert[]>>new BehaviorSubject([]);
    }

    get alerts() {
        return this._alerts.asObservable();
    }

    addAlert(alert: Alert) {
        this.dataStore.alerts.push(alert);
        alert.id = this.dataStore.alerts.length;
        this._alerts.next(Object.assign({}, this.dataStore).alerts);
        setTimeout(() => this.deleteAlert(alert), 5000);
    }

    deleteAlert(alert: Alert) {
        const index: number = this.dataStore.alerts.indexOf(alert);
        this.dataStore.alerts.splice(index, 1);
        this._alerts.next(Object.assign({}, this.dataStore).alerts);
    }
}



